.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    background-color: #ffaf80;
    padding: 10px;
}


.messages {
    display: flex;
    flex-direction: column;
    background-color: #ffd365;
    padding: 10px;
    height: 100%;
    max-height: 430px;
    overflow: auto;
}

.messages::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}
.messages::-webkit-scrollbar-button:start:decrement,
.messages::-webkit-scrollbar-button:end:increment  {
    height: 10px;
    background-color: transparent;
}
.messages::-webkit-scrollbar-track-piece  {
    background-color: #ffffff;
    -webkit-border-radius: 16px;
}
.messages::-webkit-scrollbar-thumb:vertical {
    height: 10px;
    background-color: firebrick;
    border: 1px solid firebrick;
    -webkit-border-radius: 16px;
}

.messagesAdd {
    width: 100%;
    background-color: #ffd365;
    border-top: 1px solid firebrick;

    padding-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.messagesTextarea {
    resize: none;
    height: 50px;
    border: 1px solid firebrick;
    outline: none;
    border-radius: 30px;
    padding: 15px;
    overflow: auto;
    transition: all 0.3s;
}

.messagesTextarea:focus {
    border: 1px solid #ff3e3e;
    box-shadow: 0.2em 0.2em 10px rgba(122,122,122,0.5);
}

.messagesBtn {
    margin-left: 30px;
    background-color: #fff;
    border: 2px solid firebrick;
    color: firebrick;
    cursor: pointer;
    padding: 15px 20px;
    border-radius: 30px;
    transition: all 0.1s;
}

.messagesBtn:active {
    background-color: #f6d7d7;
}

.messagesTextarea::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}
.messagesTextarea::-webkit-scrollbar-button:start:decrement,
.messagesTextarea::-webkit-scrollbar-button:end:increment  {
    height: 10px;
    background-color: transparent;
}
.messagesTextarea::-webkit-scrollbar-track-piece  {
    background-color: #ffffff;
    -webkit-border-radius: 16px;
}
.messagesTextarea::-webkit-scrollbar-thumb:vertical {
    height: 10px;
    background-color: firebrick;
    border: 1px solid firebrick;
    -webkit-border-radius: 16px;
}