
.dialog a {
    color: black;
    text-decoration: none;
    transition: all 0.3s;
}

.dialog a:hover {
    color: #ffffff;
}

.link {
    border-bottom: 1px solid firebrick;
    display: flex;
    align-items: center;
    padding: 10px 0;
}


.link img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 15px;
}

